<template>
  <div class="page">

    <page-header>
      <header-link :left="true" :edge="true" v-back-link><icon icon="back"></icon>返回</header-link>
      <header-title>组件封装</header-title>
    </page-header>


    <page-content>


      <div class='demos-content-padded'>
        <p><m-button @click.native="$refs.alert.open()">Alert</m-button></p>
        <p><m-button @click.native="$refs.confirm.open()">Confirm</m-button></p>
        <p><m-button @click.native="$refs.prompt.open()">Prompt</m-button></p>
      </div>


      <div class="content-padded">
        组件通信：
        <p>1、props 父级传给组件的数据   然后用  $emit('方法名'，数据) 返回父级数据</p>
        <p>2、vuex</p>
      </div>


      <alert :title="'Hello'" :content="'Hello there!'" :on-ok="log" ref="alert"></alert>
      <confirm :title="'Hello'" :content="'Hello there!'" :on-ok="log" ref="confirm"></confirm>
      <prompt :title="'Name'" :content="'Enter your name please!'" input="Hello!" :on-ok="log" ref="prompt"></prompt>


    </page-content>


  </div>
</template>

<script>
import Page from '../components/page'
import { Header, HeaderLink, HeaderTitle, SecondHeader } from '../components/header'
import Content from '../components/content'
import Icon from '../components/icons'
import { Button } from '../components/buttons'
import { Alert, Confirm, Prompt, Modal } from '../components/modal'




export default {
  components: {
    Page,
    'page-content': Content,
    'page-header': Header,
    Alert,
    Confirm,
    Modal,
    Prompt,
    SecondHeader,
    'm-button': Button,
    Icon,
    HeaderLink,
    HeaderTitle
  },
  mounted(){
    //this.$refs.alert.open();
  },
  data () {
    return {}
  },
  methods: {
    log (m) {
      console.log(m || 'log')
    }
  }
}
</script>

<style lang="less">

  .ball{width:100px;height:100px;background: #00b088;border-radius: 50%;text-align: center;line-height:100px;color:#fff;margin:10px 0;}


  @keyframes fadeIn{
    0%{
      opacity: 0;
    }
    100%{
      opacity: 1;
      -webkit-transform: translate3d(600px, 0, 0);
      transform: translate3d(600px, 0, 0);
    }
  }
  .fadeIn(@time:1s,@ease:ease-out,@fillmode:backwards,@count:1){
    animation-name:fadeIn;
    animation-duration:@time;
    animation-timing-function:@ease;
    animation-fill-mode:@fillmode;
    animation-iteration-count:@count;
  }


  .test-1{
  .fadeIn(3s);
  }
  .test-2{
  .fadeIn(2s,ease-in-out,forwards);
  }
  .test-3{
  .fadeIn(1s,ease-in-out,forwards,infinite);
  }


</style>



