<template>
  <div class="page">

    <page-header>
      <header-link :left="true" :edge="true" v-back-link><icon icon="back"></icon>返回</header-link>
      <header-title>axios使用</header-title>
    </page-header>


    <page-content>


      <div class="content-padded">
        <p>
          1、执行 GET 请求
        </p>

        <p>
          2、执行 POST 请求
        </p>

        <p>
         3、执行多个并发请求
        </p>


        <div>
        axios.get(url[, config])</br>

        axios.delete(url[, config])</br>

        axios.head(url[, config])</br>

        axios.post(url[, data[, config]])</br>

        axios.put(url[, data[, config]])</br>

        axios.patch(url[, data[, config]])</br>
        </div>

      </div>

      <div class="content-block">
        <p><m-button @click.native="getSimpleFun();">get请求简写</m-button></p>
        <p><m-button @click.native="postSimpleFun();">post请求简写</m-button></p>

        <p><m-button @click.native="getFun();">get请求</m-button></p>
        <p><m-button @click.native="postFun();">post请求</m-button></p>


        <p><m-button @click.native="twoHttp();">两个请求</m-button></p>



      </div>



    </page-content>


  </div>
</template>

<script>
import Page from '../components/page'
import { Header, HeaderLink, HeaderTitle, SecondHeader } from '../components/header'
import Content from '../components/content'
import Icon from '../components/icons'
import { Button, ButtonGroup } from '../components/buttons'


export default {
  components: {
    Page,
    'page-content': Content,
    'page-header': Header,
    SecondHeader,
    HeaderLink,
    Icon,
    ButtonGroup,
    'm-button': Button,
    HeaderTitle
  },
  data () {
    return {
      currentView: 'home'
    }
  },
  methods: {

    getSimpleFun () {

      this.$http.get("http://localhost:8088/vueHttp/data.php",{params:{"test":123}}).then(function(response) {

        console.log(response);

      }).catch(function(error) {

        console.log(error);

      })


    },
    postSimpleFun () {

      this.$http.post("http://localhost:8088/vueHttp/data.php","{'a':'123','b':'345'}").then(response => {

        console.log(response);

    }, response => {

      })

    },
    getFun () {



      this.$http({
        method:'get',
        url: "http://localhost:8088/vueHttp/data.php",
        params:{username:'advance',password:'123456'},
      }).then(response => {

        console.log(response);

    }, response => {

      })



    },
    postFun () {


      //键值对形式
      /* var params = new URLSearchParams();
       params.append('param1', 'value1');
       params.append('param2', 'value2');*/

      //字符串形式
      var obj="{username:'advance',password:'123456'}";


      this.$http({
        method:'post',
        url: "http://localhost:8088/vueHttp/data.php",
        data:obj
        //data:params,
      }).then(response => {

        console.log(response);

    }, response => {

      })

    },
    twoHttp(){

     var  $that=this;

      function getUserAccount() {
        return $that.$http.get('http://localhost:8088/vueHttp/data.php');
      }

      function getUserPermissions() {
        return $that.$http.get('http://localhost:8088/vueHttp/data.php');
      }

      $that.$http.all([getUserAccount(), getUserPermissions()])
              .then($that.$http.spread(function (acct, perms) {
              //两个请求现已完成
                console.log("两个请求现已完成")
            }));
    }


  }
}
</script>

<style lang="less">




</style>



