<template>
  <div class="page">

    <page-header>
      <header-link :left="true" :edge="true" v-back-link><icon icon="back"></icon>返回</header-link>
      <header-title>环境和技术</header-title>
    </page-header>


    <page-content>


      <div class="content-padded">

        <div>
          Vue 的官方是不建议直接操作 DOM 的，其优势在于视图和数据的双向绑定，而且所有DOM操作都可以用Vue实现，反而使用jQuery来操作DOM的话，会造成不必要的麻烦，DOM未渲染完成之前事件绑定不上，当然也有解决办法，比如setTimeOut
        </div>


      <p>1、axios 支持 Promise的http请求</p>
      <p>2、vue-cli相当于脚手架 给你自动生成模板工程</p>
      <p>3、vue-router是 vue路由插件 支持你单页应用的</p>
      <p>4、vue-loader是webpack下loader插件 可以把.vue文件 输出成组件</p>
      <p>5、vuex Vue应用程序开发的状态和数据管理模式 适用大型vue应用</p>
      <p>6、调试工具：chrome插件 vue-devtools</p>


      </div>




    </page-content>


  </div>
</template>

<script>
import Page from '../components/page'
import { Header, HeaderLink, HeaderTitle, SecondHeader } from '../components/header'
import Icon from '../components/icons'
import Content from '../components/content'


export default {
  components: {
    Page,
    'page-content': Content,
    'page-header': Header,
    SecondHeader,
    HeaderLink,
    Icon,
    HeaderTitle
  },
  data () {
    return {
      currentView: 'home'
    }
  },
  methods: {

  }
}
</script>

<style lang="less">

  .ball{width:100px;height:100px;background: #00b088;border-radius: 50%;text-align: center;line-height:100px;color:#fff;margin:10px 0;}


  @keyframes fadeIn{
    0%{
      opacity: 0;
    }
    100%{
      opacity: 1;
      -webkit-transform: translate3d(600px, 0, 0);
      transform: translate3d(600px, 0, 0);
    }
  }
  .fadeIn(@time:1s,@ease:ease-out,@fillmode:backwards,@count:1){
    animation-name:fadeIn;
    animation-duration:@time;
    animation-timing-function:@ease;
    animation-fill-mode:@fillmode;
    animation-iteration-count:@count;
  }


  .test-1{
  .fadeIn(3s);
  }
  .test-2{
  .fadeIn(2s,ease-in-out,forwards);
  }
  .test-3{
  .fadeIn(1s,ease-in-out,forwards,infinite);
  }


</style>



